"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"

const albumCategories = {
  wedding: [
    { id: 1, title: "Royal Wedding", description: "Traditional Indian wedding album", images: 250 },
    { id: 2, title: "Modern Love", description: "Contemporary wedding photography", images: 180 },
    { id: 3, title: "Destination Wedding", description: "Beach wedding memories", images: 220 },
  ],
  baby: [
    { id: 4, title: "Newborn Bliss", description: "First days of life", images: 150 },
    { id: 5, title: "First Birthday", description: "Milestone celebration", images: 120 },
    { id: 6, title: "Baby Shower", description: "Pre-birth celebration", images: 100 },
  ],
  birthday: [
    { id: 7, title: "Sweet 16", description: "Teenage milestone", images: 200 },
    { id: 8, title: "Golden 50th", description: "Milestone birthday", images: 160 },
    { id: 9, title: "Kids Party", description: "Fun celebration", images: 140 },
  ],
}

export default function GalleryPage() {
  const [selectedAlbum, setSelectedAlbum] = useState<{
    id: number
    title: string
    description: string
    images: number
  } | null>(null)

  return (
    <div className="min-h-screen py-8">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold mb-4">Our Gallery</h1>
          <p className="text-lg text-gray-600">Explore our premium album collections</p>
        </div>

        <Tabs defaultValue="wedding" className="w-full">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="wedding">Wedding Albums</TabsTrigger>
            <TabsTrigger value="baby">Baby Albums</TabsTrigger>
            <TabsTrigger value="birthday">Birthday Albums</TabsTrigger>
          </TabsList>

          {Object.entries(albumCategories).map(([category, albums]) => (
            <TabsContent key={category} value={category} className="mt-6">
              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                {albums.map((album) => (
                  <Card key={album.id} className="overflow-hidden hover:shadow-lg transition-shadow">
                    <div className="aspect-[4/3] bg-gradient-to-br from-amber-100 to-amber-200 flex items-center justify-center">
                      <span className="text-amber-600 font-semibold">{album.title}</span>
                    </div>
                    <CardHeader>
                      <CardTitle>{album.title}</CardTitle>
                      <CardDescription>{album.description}</CardDescription>
                    </CardHeader>
                    <CardContent>
                      <p className="text-sm text-gray-600 mb-4">{album.images} photos</p>
                      <Button 
                        className="w-full" 
                        onClick={() => setSelectedAlbum(album)}
                      >
                        View Album
                      </Button>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </TabsContent>
          ))}
        </Tabs>
      </div>

      <Dialog open={selectedAlbum !== null} onOpenChange={() => setSelectedAlbum(null)}>
        <DialogContent className="max-w-4xl max-h-[90vh]">
          <DialogHeader>
            <DialogTitle>{selectedAlbum?.title}</DialogTitle>
            <DialogDescription>
              {selectedAlbum?.description} - {selectedAlbum?.images} photos
            </DialogDescription>
          </DialogHeader>
          <div className="grid grid-cols-2 md:grid-cols-3 gap-4 overflow-y-auto p-4">
            {Array.from({ length: Math.min(selectedAlbum?.images || 0, 12) }).map((_, index) => (
              <div key={index} className="aspect-square bg-gradient-to-br from-gray-100 to-gray-200 flex items-center justify-center">
                <span className="text-gray-500 text-sm">Photo {index + 1}</span>
              </div>
            ))}
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
